ul {
    padding: 0
}

ul li {
    list-style: none;
}

button {
    cursor: pointer;
    border: 2px solid gray;
    border-radius: 5px;
    padding: 5px;
    background-color: transparent;
}

.app-wrapper {
    margin: 0 auto;
    display: grid;
    max-width: 700px;

    grid-template-areas:
    "h h"
    "n c";

    grid-template-rows: 60px 1fr;
    grid-template-columns: 2fr 10fr;
    grid-gap: 10px;
}

.app-wrapper-content {
    grid-area: c;
    background-color: rgb(184, 204, 241);
    padding: 15px
}

.preloader-container {
    max-width: 700px;
    margin: 0 auto;
    background-color: rgb(184, 204, 241);
}